.info-block {
	padding: 154px 0 100px;
	color: $white;
	background: $black;
	@include sm {
		padding: 100px 0; }
	&__header {
		margin: 0 auto 68px;
		max-width: 970px;
		text-align: center;
		p {
			opacity: 0.8; } }
	&__title {
		margin-bottom: 21px; }
	&__logo {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 11px;
		width: 48px; }
	&__content {
		margin: 0 auto 86px;
		max-width: 797px; }
	&__graph {
		padding: 20px 0;
		margin: 0 auto;
		max-width: 1000px; } }
