@font-face {
  font-family: 'Karla';
  font-style: italic;
  font-weight: 400;
  src: local("Karla Italic"), local("Karla-Italic"), url("../fonts/karla-italic.woff2") format("woff2"), url("../fonts/karla-italic.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 400;
  src: local("Karla"), local("Karla-Regular"), url("../fonts/karla-regular.woff2") format("woff2"), url("../fonts/karla-regular.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: italic;
  font-weight: 700;
  src: local("Karla Bold Italic"), local("Karla-BoldItalic"), url("../fonts/karla-bolditalic.woff2") format("woff2"), url("../fonts/karla-bolditalic.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  src: local("Karla Bold"), local("Karla-Bold"), url("../fonts/karla-bold.woff2") format("woff2"), url("../fonts/karla-bold.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
}

.icon-doc {
  width: 1em;
  height: 1em;
  fill: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-linkedin-full {
  width: 1em;
  height: 1em;
  fill: #BEBEBE;
}

.icon-telegram-full {
  width: 1em;
  height: 1em;
  fill: #BEBEBE;
}

.icon-twitter-full {
  width: 1em;
  height: 1em;
  fill: #BEBEBE;
}

body {
  font-family: 'Karla', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  background: #000;
}

.inner {
  position: relative;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1200px;
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.logo {
  float: left;
  margin-top: 16px;
}

.menu {
  float: right;
  margin-top: 20px;
}

.menu li {
  vertical-align: top;
  display: inline-block;
}

.menu li:not(:first-child) {
  margin-left: 28px;
}

.menu a {
  font-size: 13px;
  font-weight: 700;
  line-height: 1.15385;
  color: #fff;
  transition: color .3s ease-in-out;
}

.no-touch .menu a:hover {
  color: #35A8FF;
}

.title-lg {
  font-size: 36px;
  line-height: 1.16667;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 30px;
  }
}

.sm-description {
  font-size: 20px;
  line-height: 1.5;
}

@media only screen and (max-width: 1023px) {
  .sm-description {
    font-size: 18px;
  }
}

@media only screen and (max-width: 767px) {
  .sm-description {
    font-size: 16px;
  }
}

a {
  text-decoration: none;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.info-block {
  padding: 154px 0 100px;
  color: #fff;
  background: #000;
}

@media only screen and (max-width: 767px) {
  .info-block {
    padding: 100px 0;
  }
}

.info-block__header {
  margin: 0 auto 68px;
  max-width: 970px;
  text-align: center;
}

.info-block__header p {
  opacity: 0.8;
}

.info-block__title {
  margin-bottom: 21px;
}

.info-block__logo {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 11px;
  width: 48px;
}

.info-block__content {
  margin: 0 auto 86px;
  max-width: 797px;
}

.info-block__graph {
  padding: 20px 0;
  margin: 0 auto;
  max-width: 1000px;
}

.info-list__item {
  padding: 16px 0;
}

.info-list__item:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 767px) {
  .info-list__item {
    padding: 10px 0;
  }
}

.info-list__title {
  width: 37.2%;
  color: #35A8FF;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 0.2em;
  text-align: right;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .info-list__title {
    text-align: left;
  }
}

.info-list__description {
  width: 62.8%;
  font-size: 16px;
  line-height: 1.5;
  text-align: left;
}

@media only screen and (max-width: 767px) {
  .info-list__description {
    padding-top: 4px;
  }
}

.info-list__title, .info-list__description {
  float: left;
  padding-left: 15px;
  padding-right: 15px;
}

@media only screen and (max-width: 767px) {
  .info-list__title, .info-list__description {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }
}

.info-list__title,
.info-list__description p {
  padding-top: 5px;
  padding-bottom: 5px;
}

@media only screen and (max-width: 767px) {
  .info-list__title,
  .info-list__description p {
    padding-top: 2px;
    padding-bottom: 2px;
  }
}

.sm-graph_green .sm-graph__title {
  color: #36CD98;
}

.sm-graph_green .sm-graph__line:before, .sm-graph_green .sm-graph__line-in {
  background: #36CD98;
}

.sm-graph_blue .sm-graph__title {
  color: #35A8FF;
}

.sm-graph_blue .sm-graph__line:before, .sm-graph_blue .sm-graph__line-in {
  background: #35A8FF;
}

.sm-graph__title {
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.22222;
}

.sm-graph__container {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.sm-graph__row {
  display: table-row;
}

.sm-graph__box {
  display: table-cell;
  min-width: 67px;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__box {
    width: 100% !important;
    margin-bottom: 20px;
  }
}

.sm-graph__box:nth-child(even) .sm-graph__line:before {
  opacity: 0.5;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__container, .sm-graph__row, .sm-graph__box {
    display: block;
  }
}

.sm-graph__line {
  position: relative;
  padding: 0 1px;
  height: 11px;
  opacity: 0.5;
}

.sm-graph__line:before {
  content: '';
  position: absolute;
  top: 0;
  left: 1px;
  right: 1px;
  bottom: 0;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__line:before {
    opacity: 0.5;
  }
}

.sm-graph__line-in {
  display: none;
  height: 100%;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__line-in {
    display: block;
  }
}

.sm-graph__info {
  padding: 8px 15px 0 1px;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__info {
    padding: 10px 0 0;
  }
}

.sm-graph__value, .sm-graph__description {
  padding: 5px 0;
}

@media only screen and (max-width: 1200px) {
  .sm-graph__value, .sm-graph__description {
    padding: 0;
  }
}

.sm-graph__value {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
}

.sm-graph__description {
  font-size: 14px;
  line-height: 1.57143;
}

.team {
  padding: 150px 0 80px;
  background: #EBEBEB;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .team {
    padding: 100px 0 80px;
  }
}

.team__title {
  margin-bottom: 50px;
}

.team__svg {
  position: relative;
  overflow: hidden;
}

.team__svg svg {
  position: absolute;
  top: 0;
  left: 0;
}

.team__members {
  margin: 0 auto;
  max-width: 1200px;
}

.team__members:after {
  content: " ";
  display: table;
  clear: both;
}

.team__member {
  float: left;
  width: 33.33%;
}

@media only screen and (max-width: 1023px) {
  .team__member {
    float: none;
    margin: 0 auto 30px;
    width: 100%;
    max-width: 400px;
  }
}

.member {
  padding: 20px 15px;
  text-align: center;
}

.member__img {
  margin-bottom: 25px;
}

.member__img-in {
  width: 110%;
  transform: translate3d(-5%, 0, 0);
}

.member__title {
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.1875;
}

.member__description {
  margin-bottom: 13px;
  color: #979797;
  font-size: 16px;
  line-height: 1.5;
}

.member__social {
  font-size: 0;
}

.member__social a {
  display: inline-block;
  vertical-align: top;
  padding: 10px;
  color: #fff;
  font-size: 24px;
}

.no-touch .member__social a:hover svg {
  fill: #a5a5a5;
}

.member__social a svg {
  fill: #BEBEBE;
  transition: fill 0.175s ease-in-out;
}
