.sm-graph {
	&_green {
		.sm-graph {
			&__title {
				color: $green; }
			&__line {
				&:before, &-in {
					background: $green; } } } }
	&_blue {
		.sm-graph {
			&__title {
				color: $blue; }
			&__line {
				&:before, &-in {
					background: $blue; } } } }
	&__title {
		margin-bottom: 20px;
		@include f(18, 22, null, 700); }
	&__container {
		display: table;
		table-layout: fixed;
		width: 100%; }
	&__row {
		display: table-row; }
	&__box {
		display: table-cell;
		min-width: 67px;
		@include lg {
			width: 100% !important;
			margin-bottom: 20px; }
		&:nth-child(even) {
			.sm-graph {
				&__line {
					&:before {
						opacity: 0.5; } } } } }
	&__container,
	&__row,
	&__box {
		@include lg {
			display: block; } }
	&__line {
		position: relative;
		padding: 0 1px;
		height: 11px;
		opacity: 0.5;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 1px;
			right: 1px;
			bottom: 0;
			@include lg {
				opacity: 0.5; } }
		&-in {
			display: none;
			height: 100%;
			@include lg {
				display: block; } } }
	&__info {
		padding: 8px 15px 0 1px;
		@include lg {
			padding: 10px 0 0; } }
	&__value,
	&__description {
		padding: 5px 0;
		@include lg {
			padding: 0; } }
	&__value {
		@include f(16, 24, null, 700); }
	&__description {
		@include f(14, 22); } }
